/*** The new CSS Reset - version 1.3.1 (last updated 28.10.2021) ***/

/*
    Remove all the styles of the "User-Agent-Stylesheet", except for the 'display' property
    - The "symbol *" part is to solve Firefox SVG sprite bug
 */

*:where(:not(iframe):not(canvas):not(img):not(svg):not(video):not(svg *):not(symbol *)) {
  all: unset;
  display: revert;
}

/* Preferred box-sizing value */

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove list styles (bullets/numbers) */

ol,
ul {
  list-style: none;
}

/* For images to not be able to exceed their container */

img {
  max-width: 100%;
}

/* removes spacing between cells in tables */

table {
  border-collapse: collapse;
}

/* revert the 'white-space' property for textarea elements on Safari */

textarea {
  white-space: revert;
}

[data-scroll-container],
[data-scroll-section] {
  transform-style: preserve-3d;
  will-change: transform;
}

[data-scroll-container] {
  width: 100%;
  position: fixed;
  overflow-y: unset !important;
}

img {
  max-width: 100%;
  display: block;
  object-fit: cover;
  height: auto;
}

html {
  font-size: 62.5%;
}

body {
  font-family: 'Circular Std', sans-serif;
  color: #353535;
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 2.2rem;
  letter-spacing: -.02rem;
}

strong {
  font-weight: 700;
}

html,
body {
  max-width: 100%;
  overflow-x: hidden;
}

.overflow-y-hidden {
  overflow-y: hidden;
}

a,
button {
  cursor: pointer;
  transition: color .3s, background-color .3s, border-color .3s;
}

p {
  line-height: 2.2rem;
  font-weight: 400;
}

svg {
  width: 2rem;
  height: 2rem;
}

.container,
.container-md,
.container-sm {
  width: 100%;
  max-width: calc(188rem + (5.5rem * 2));
  padding: 0 5.5rem;
  margin: 0 auto;
}

.container-md {
  max-width: calc(170rem + (2.5rem * 2));
}

.container-sm {
  max-width: calc(116.4rem + (2.5rem * 2));
}

.is--show {
  display: block !important;
}

.is--hide {
  display: none !important;
}

.hidden-heading {
  font-size: 0;
  width: 1px;
  height: 1px;
  display: inline-block;
  overflow: hidden;
  position: absolute !important;
  border: 0 !important;
  padding: 0 !important;
  margin: 0 !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
}

.visually-hidden {
  position: absolute !important;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}

.visually-shown {
  position: inherit !important;
  overflow: auto;
  clip: auto;
  width: auto;
  height: auto;
  margin: 0;
}

.label-hidden {
  position: absolute !important;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}

.no-placeholder .label-hidden {
  position: inherit !important;
  overflow: auto;
  clip: auto;
  width: auto;
  height: auto;
  margin: 0;
}

.no-scroll-y {
  overflow-y: hidden;
}

.fade-in {
  transform: translateY(6rem) !important;
  opacity: .001 !important;
  transition: opacity 0.8s cubic-bezier(0.4, 0, 0.68, 0.06), transform 1.2s cubic-bezier(0.77, 0, 0.18, 1) !important;
}

.fade-in.is-inview {
  font-size: 2.4rem;
  transform: translateY(0) !important;
  opacity: 1 !important;
  line-height: 3.6rem;
  letter-spacing: -.08rem;
}

.s-header {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1000000;
  width: 100%;
  padding-top: 5rem;
  background-color: transparent;
}

.s-header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.s-header__logo {
  cursor: pointer !important;
}

.s-header__logo__img {
  width: 11rem;
}

.s-header__menu {
  display: flex;
  align-items: center;
}

.s-header__menu__item:not(:last-of-type) {
  margin-right: 5rem;
}

.s-header__menu__item.active .s-header__menu__link {
  font-weight: 700;
}

.s-header__menu__item.active .s-header__menu__link::before {
  width: 100%;
}

.s-header__menu__item--jobs .s-header__menu__link {
  display: inline-flex;
  align-items: center;
}

.s-header__menu__link {
  position: relative;
  cursor: default;
  font-size: 2.4rem;
  line-height: 3.6rem;
  letter-spacing: -.08rem;
  color: #FFFFFF;
}

.s-header__menu__link::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  z-index: -1;
  width: 0;
  height: 0.2rem;
  background: #FFFFFF;
  transition: width 0.3s;
}

.s-header__menu__link:hover {
  cursor: pointer;
}

.s-header__menu__link:hover::before {
  width: 100%;
}

.s-header__menu__qty {
  flex-shrink: 0;
  position: relative;
  display: inline-flex !important;
  align-items: center;
  justify-content: center;
  width: 2.5rem;
  height: 2.5rem;
  margin-left: 1rem;
  font-size: 1.4rem;
  font-weight: 400;
  line-height: 2.1rem;
  font-weight: 700;
  color: #FFFFFF;
  background-color: #FF6719;
  border-radius: 50%;
}

.s-header__btn-mobile {
  display: none;
  width: 3.6rem;
  height: 3.6rem;
  color: #FFFFFF;
}

.s-header__btn-mobile svg {
  width: 100%;
  height: 100%;
}

.s-header__btn-mobile svg.btn-menu-close {
  display: none;
}

/* Logo branco */

.page-template-page-servicos .s-header__logo,
.page-template-page-trabalhos .s-header__logo,
.page-template-page-contato .s-header__logo,
.page-template-page-vagas .s-header__logo {
  filter: brightness(0) invert(1);
}

.page-template-page-servicos .s-header__menu__qty,
.page-template-page-trabalhos .s-header__menu__qty,
.page-template-page-contato .s-header__menu__qty,
.page-template-page-vagas .s-header__menu__qty {
  background: #B84700;
}

/* Textos pretos */

.page-template-page-servicos-detalhes .s-header__menu__link,
.page-template-page-gps .s-header__menu__link {
  color: #353535;
}

.page-template-page-servicos-detalhes .s-header__menu__link:before,
.page-template-page-gps .s-header__menu__link:before {
  background-color: #353535;
}

body.menu-opened .s-header__menu {
  opacity: 1;
}

body.menu-opened .s-header__menu__link span {
  transform: translateY(0);
  transition: transform 1s cubic-bezier(0.77, 0, 0.175, 1) 300ms;
}

body.menu-opened .s-header__menu__item--talk::before {
  opacity: 1;
}

body.menu-hide .s-header__nav {
  display: none;
}

.s-footer {
  position: relative;
  background-color: #FF6719;
  color: #FFFFFF;
  padding: 25rem 0 32rem;
  z-index: -1;
  background: url(../images/footer-bg.png) center/cover no-repeat;
}

.s-footer::before {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 10rem;
  background: #FFFFFF;
}

.s-footer::after {
  content: '';
  position: absolute;
  left: 50%;
  bottom: 1.8rem;
  transform: translateX(-50%);
  width: 100%;
  height: 6rem;
  background: url(../images/footer-detail.svg) center/contain repeat-x;
}

.s-footer__wrapper {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}

.s-footer__column-1 {
  max-width: 50.8rem;
  width: 100%;
  margin-right: 3rem;
}

.s-footer__column-1 .btn-secondary {
  margin-top: 6rem;
}

.s-footer__title {
  margin-bottom: 3rem;
  font-weight: 700;
  font-size: 10rem;
  line-height: 9rem;
  letter-spacing: -0.5rem;
}

.s-footer__text {
  font-size: 2rem;
  line-height: 2.8rem;
  letter-spacing: -.05rem;
  color: #FFDECC;
}

.s-footer__column-2 {
  max-width: 104.3rem;
  width: 100%;
}

.s-footer__top__wrapper {
  display: flex;
  justify-content: space-between;
}

.s-footer__top__content {
  max-width: 31.4rem;
  width: 100%;
  margin-right: 3rem;
}

.s-footer__subtitle {
  margin-bottom: 2rem;
  font-size: 2.4rem;
  line-height: 3.6rem;
  letter-spacing: -.08rem;
  font-weight: 700;
}

.s-footer__work {
  max-width: 62.9rem;
  width: 100%;
}

.s-footer__work__wrapper {
  display: flex;
  align-items: flex-start;
}

.s-footer__work .btn-secondary {
  flex-shrink: 0;
  margin-left: 6rem;
}

.s-footer__bottom {
  display: flex;
  align-items: flex-start;
  margin-top: 6rem;
}

.s-footer__bottom__content:nth-of-type(2) {
  display: none;
}

.s-footer__bottom__content:not(:last-of-type) {
  margin-right: 25.5rem;
}

.s-footer__link {
  display: inline-flex;
  align-items: center;
  font-size: 2rem;
  line-height: 2.8rem;
  letter-spacing: -.05rem;
  color: #FFDECC;
}

.s-footer__link svg {
  width: 2.4rem;
  height: 2.4rem;
  margin-right: 1rem;
}

.s-footer .btn-secondary {
  color: #FFFFFF !important;
  border-color: #FFFFFF !important;
}

.s-footer__social {
  display: flex;
  align-items: center;
}

.s-footer__social__item:not(:last-of-type) {
  margin-right: 3rem;
}

.s-footer__social__link:hover {
  color: #B84700;
}

.s-footer__social__link svg {
  width: 2.4rem;
  height: 2.4rem;
}

.grecaptcha-badge {
  display: none;
}

.btn-lg {
  display: inline-block;
  padding: 3rem 5rem;
  font-weight: 700;
  font-size: 2.2rem;
  text-align: center;
  line-height: 1.8rem;
  letter-spacing: -0.02em;
  border-radius: .8rem;
}

.btn-md {
  display: inline-block;
  padding: 2rem 4rem;
  font-weight: 700;
  font-size: 2rem;
  text-align: center;
  line-height: 2.5rem;
  letter-spacing: -.05rem;
  border-radius: .5rem;
}

.btn-primary {
  background: #FF6719;
  color: #FFFFFF;
}

.btn-primary:hover {
  background: #B84700;
}

.btn-secondary {
  border: 0.2rem solid #FF6719;
  color: #FF6719;
}

.btn-secondary:hover {
  color: #B84700;
  border-color: #B84700;
}

.btn-secondary.btn-lg {
  border-width: .3rem;
}

.s-hero {
  position: relative;
  display: flex;
  min-height: 59.3rem;
  height: auto;
}

.s-hero::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  transform: rotate(-180deg);
  width: 100%;
  height: 100%;
  background: linear-gradient(119.96deg, #FC2D35 -16.45%, #FF771F 69.16%);
}

.s-hero > * {
  position: relative;
  z-index: 100;
}

.s-hero__img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  mix-blend-mode: soft-light;
  opacity: .45;
}

.s-hero .container {
  min-height: 100%;
}

.s-hero__wrapper {
  display: flex;
  align-items: flex-end;
  width: 100%;
  height: 100%;
  padding: 10rem 0;
}

.s-hero__flex {
  display: flex;
}

.s-hero__group {
  max-width: 59.7rem;
  width: 100%;
  margin-right: 3rem;
}

.s-hero__title {
  flex-shrink: 0;
  max-width: 59.7rem;
  width: 100%;
  margin-right: 3rem;
  font-size: 4.8rem;
  font-weight: 400;
  line-height: 4.8rem;
  letter-spacing: -.2rem;
  font-weight: 700;
  color: #FFFFFF;
}

.s-hero__subtitle {
  font-size: 2.4rem;
  line-height: 3.6rem;
  letter-spacing: -.08rem;
  color: #CCCCCC;
}

.s-hero__text {
  max-width: 80.6rem;
  width: 100%;
}

.s-hero__text > * {
  font-size: 2.4rem;
  line-height: 3.6rem;
  letter-spacing: -.08rem;
  color: #FFDECC;
}

.s-hero__text > *:not(:last-of-type) {
  margin-bottom: 2rem;
}

.full-box-content {
  padding: 10rem 0;
  background-color: #F4F4F4;
}

.full-box-content__wrapper {
  display: flex;
}

.full-box-content__title {
  max-width: 27.9rem;
  width: 100%;
  margin-right: 4.9rem;
  font-size: 3.2rem;
  font-weight: 400;
  line-height: 3.8rem;
  letter-spacing: -.1rem;
}

.full-box-content__text {
  flex-grow: 1;
}

.full-box-content__text p {
  font-size: 2rem;
  line-height: 2.8rem;
  letter-spacing: -.05rem;
  color: #777777;
}

.full-box-content__text p:not(:last-of-type) {
  margin-bottom: 2.8rem;
}

.form__field {
  margin-bottom: 4rem;
}

.form__checkbox:not(:last-of-type) {
  margin-bottom: 2rem;
}

.form__checkbox label {
  display: flex;
  align-items: flex-start;
}

.form__checkbox__input {
  position: relative;
  top: .3rem;
  flex-shrink: 0;
  width: 2rem;
  height: 2rem;
  margin-right: 2rem;
  border: 0.2rem solid #EBEBEB;
  border-radius: .4rem;
  transition: all .3s;
  outline: .2rem solid transparent;
  outline-offset: .2rem;
}

.form__checkbox__input::before {
  content: '';
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 1.123rem;
  height: .875rem;
  background: url(../images/icons/checked.svg) center/cover no-repeat;
  pointer-events: none;
}

.form__checkbox__input:hover {
  border-color: #FF6719;
}

.form__checkbox__input:focus {
  outline: 0.2rem solid #FF6719;
}

.form__checkbox__input:checked {
  background: #FF6719;
  border-color: #FF6719;
}

.form__checkbox__input:checked:hover {
  background: #73AA13;
  border-color: #73AA13;
}

.form__checkbox__input.invalid {
  border-color: #DC1212 !important;
}

.form__checkbox__label {
  font-size: 2rem;
  line-height: 2.8rem;
  letter-spacing: -.05rem;
  color: #353535;
}

.form__input {
  width: 100%;
  padding: 15.77px 10px 16.23px;
  font-size: 2rem;
  line-height: 2.8rem;
  letter-spacing: -.05rem;
  color: #999999;
  border-bottom: 0.2rem solid #EBEBEB;
  transition: border-color 300ms;
}

.form__input:focus {
  border-color: #FF6719;
}

.form__input.invalid {
  border-color: #DC1212 !important;
}

.form__select {
  position: relative;
}

.form__select::after {
  content: '';
  position: absolute;
  right: 1.5rem;
  top: 50%;
  transform: translateY(-50%);
  width: 1.6rem;
  height: 1.6rem;
  background: url(../images/icons/down-chevron.svg) center/cover no-repeat;
}

.form__select .form__input {
  border: 0.2rem solid #EBEBEB;
  border-radius: .5rem;
}

.form__file__container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.6rem;
  border: 0.2rem dashed #EBEBEB;
  border-radius: .5rem;
  cursor: pointer;
}

.form__file__container.active .form__file__close {
  display: inline-flex;
}

.form__file__input {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}

.form__file__icon {
  width: 2rem;
  height: 2rem;
  margin-right: 2rem;
}

.form__file__label {
  font-size: 2rem;
  line-height: 2.8rem;
  letter-spacing: -.05rem;
  color: #FF6719;
}

.form__file__close {
  position: absolute;
  right: 1rem;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1000;
  display: none;
  align-items: center;
  justify-content: center;
  width: 2.5rem;
  height: 2.5rem;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 1.3rem;
  line-height: 1;
  border-radius: 50%;
  background-color: #FF6719;
}

.form__textarea {
  height: 20rem;
  border: 0.2rem solid #EBEBEB;
  border-radius: .4rem;
}

.form [type=submit] {
  position: relative;
}

.form [type=submit]::before {
  content: '';
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: none;
  width: 3rem;
  height: 3rem;
  background: url(../images/icons/loading.svg) center/cover no-repeat;
  animation: rotate 1s ease-in infinite;
}

.form [type=submit].loading {
  color: #FFFFFF;
  cursor: not-allowed;
}

.form [type=submit].loading::before {
  display: inline-block;
}

@keyframes rotate {
  from {
    transform: translate(-50%, -50%) rotate(0deg);
  }

  to {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}

.content-i__top {
  display: flex;
  align-items: flex-end;
}

.content-i__title {
  width: 100%;
  max-width: 32.8rem;
  width: 100%;
  margin-right: 2.9rem;
  font-size: 4.8rem;
  font-weight: 400;
  line-height: 4.8rem;
  letter-spacing: -.2rem;
}

.content-i__text {
  font-size: 2rem;
  line-height: 2.8rem;
  letter-spacing: -.05rem;
  color: #777;
  flex-grow: 1;
}

.content-i__text p {
  font-size: 2rem;
