.stamp {
  position: relative;
  width: 26.9rem;
  height: 26.9rem;
}

.stamp__img {
  position: relative;
  width: 100%;
  border-radius: 50%;
  z-index: 100;
  box-shadow: 0px 35px 30px rgba(0, 0, 0, 0.05), 0px 15px 8px rgba(0, 0, 0, 0.04), 0px 8px 5px rgba(0, 0, 0, 0.03), 0px 5px 2px rgba(0, 0, 0, 0.0196802);
}

.stamp__detail {
  position: absolute;
  left: 50%;
  top: 50%;
  height: 38.7rem;
  width: 38.7rem;
  border-radius: 50%;
  border: 1px solid #EBEBEB;
  animation-name: pulse;
  animation-duration: 2s;
  animation-delay: -0.5s;
  animation-iteration-count: infinite;
}

.stamp__detail::before,
.stamp__detail::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 63.1rem;
  height: 63.1rem;
  border-radius: 50%;
  border: 1px solid #EBEBEB;
}

.stamp__detail::after {
  width: 101.5rem;
  height: 101.5rem;
  border: 0.1rem solid #EBEBEB;
}

@keyframes pulse {
  0% {
    opacity: 0.75;
    transform: translate(-50%, -50%) scale(0.5);
  }

  100% {
    opacity: 0;
    transform: translate(-50%, -50%) scale(1);
  }
}

.cursor {
  position: fixed;
  left: -6rem;
  top: -6rem;
  z-index: 10000000;
  transform: translate3d(0, 0, 0) scale(0.3);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 12rem;
  height: 12rem;
  will-change: transform;
  pointer-events: none;
  backdrop-filter: blur(2rem);
  background-color: rgba(255, 103, 25, 0.5);
  overflow: hidden;
  border-radius: 50%;
  transform-origin: center;
  opacity: 0;
  mix-blend-mode: hard-light;
  transition: all .1s;
}

